<template>
  <div align="center">
    <div class="form-group col-lg-6" align="center">
      <label for="title">제목</label>
      <input type="text" class="form-control" id="title" ref="title" placeholder="제목을 입력하세요" v-model="qnaTitle">
      <label for="content">내용</label>
      <textarea type="text" class="form-control" id="contnet" ref="content" placeholder="내용을 입력하세요"
        v-model="qnaContent"></textarea>
      <label for="writer">비밀번호</label>
      <input type="password" class="form-control" id="password" ref="password" placeholder="비밀번호를 입력하세요" v-model="qnaPassword">
    </div>
    <div class="text-center">
      <button class="btn btn-primary" @click="checkHandler">등록</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>   
import axios from 'axios';
export default {
    name : 'Create',
    data: function () {
    return {
      no: '',
      regtime: '',
      password: '',
      title: '',
      content: '',
    };
  },
  methods: {
    checkHandler() {
      if(!this.qnaTitle){
        alert("제목을 입력하시오");
      }else if(!this.qnaContent){
        alert("내용을 입력하시오");
      }else if(!this.qnaPassword){
        alert("비밀번호를 입력하시오");
      }
      else this.createHandler();
    },
    createHandler() {
      axios
        .post('http://localhost:9999/happyhouse/api/qna', {
          qnaPassword: this.qnaPassword,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
        })
        .then(({ data }) => {
          let msg = '등록 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '등록이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        });
    },
    moveList() {
      this.$router.push('/list');
    },
  },
}
</script>

