<template>
  <div align="center">
            <div class="form-group col-lg-6" align="center">
              <label for="title">제목</label>
              <input type="text" class="form-control" id="qnaTitle" ref="qnaTitle" placeholder="제목을 입력하세요" v-model="qnaTitle">
            
              <label for="content">내용</label>
              <textarea type="text" class="form-control" id="qnaContnet" ref="qnaContent" placeholder="내용을 입력하세요"
                v-model="qnaContent"></textarea>
            
              <label for="writer">비밀번호</label>
              <input type="password" class="form-control" id="qnaPassword" ref="qnaPassword" placeholder="비밀번호를 입력하세요" v-model="qnaPassword">
            </div>
            <div class="text-center">
              <button class="btn btn-primary" @click="checkHandler">수정</button>
            </div>
          </div>
</template>

<script>
import axios from 'axios';
// import moment from 'moment';
export default {
    name: 'Update',
data: function () {
    return {
      qnaNo: '',
      qnaDatetime: '',
      qnaPassword: '',
      qnaTitle: '',
      qnaContent: '',
    };
  },
  methods: {
    checkHandler() {
      if(!this.qnaTitle){
        alert("제목을 입력하시오");
      }
      else if(!this.qnaContent){
        alert("내용을 입력하시오");
      } else if(!this.qnaPassword){
        alert("비밀번호를 입력하시오");
      }
      else this.updateHandler();
    },
    updateHandler() {
      axios
        .put(`http://localhost:9999/happyhouse/api/qna/${this.qnaNo}`, {
          qnaNo: this.qnaNo,
          qnaDatetime: this.qnaDatetime,
          qnaPassword: this.qnaPassword,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
        })
        .then(({ data }) => {
          let msg = '수정 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '수정이 완료되었습니다.';
          }
          alert(msg);
          this.$router.push('/list');
        });
    },
  },
  created() {
    axios.get(`http://localhost:9999/happyhouse/api/qna/${this.$route.query.qnaNo}`).then(({ data }) => {
      this.qnaNo = data.qnaNo;
      this.qnaDatetime = data.qnaDatetime;
      this.qnaPassword = data.qnaPassword;
      this.qnaTitle = data.qnaTitle;
      this.qnaContent = data.qnaContent;
    });
  },
}
</script>

