import Vue from "vue";
import VueRouter from "vue-router";
import Index from '@/components/Index.vue';  // @는 src에서 찾기 .은 현재폴더 ..는 이전폴더
import List from '@/components/List.vue';
import Create from '@/components/Create.vue';
import Read from '@/components/Read.vue';
import Update from '@/components/Update.vue';
import Delete from '@/components/Delete.vue';  
import Usercheck from '@/components/Usercheck.vue';  

Vue.use(VueRouter);

export default new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
    },
    {
      path: '/list',
      name: 'list',
      component: List,
    },
    {
      path: '/create',
      name: 'create',
      component: Create,
    },
    {
      path: '/read',
      name: 'read',
      component: Read,
    },
    {
      path: '/update',
      name: 'update',
      component: Update,
    },
    {
      path: '/delete/:qnaNo',
      name: 'delete',
      component: Delete,
    },
    {
      path: '/usercheck',
      name: 'usercheck',
      component: Usercheck,
    },
  ],
});
