<template>
  <div>
    <form id="loginform" method="post" action="">
      <div class="form-group" align="center">
        <br>
      <label align="center" for="writer"><h4>비밀번호를 입력하세요</h4></label>
      <input type="password" align="center" class="form-control" style="width: 50%;" id="password" ref="password" placeholder="비밀번호를 입력하세요" v-model="Passwordcheck" >
    </div>
    </form>
    <div class="text-center">
      <button class="btn btn-primary" @click="checkHandler">입력</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import moment from 'moment';
export default {
name: 'usercheck',
  data: function () {
    return {
      item: {},
      Passwordcheck : ''
    };
  },
  created() {
    // const params = new URL(document.location).searchParams;
    axios.get(`http://localhost:9999/happyhouse/api/qna/${this.$route.params.qnaNo}`).then(({ data }) => {
      this.item = data;
      console.log(data)
    });
  },
   methods: {
     getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
    },
    checkHandler() {
      if(!this.Passwordcheck){
        alert("비밀번호를 입력하시오");
      }
      else if(this.Passwordcheck!=this.item.qnaPassword){
        alert("비밀번호가 틀렸습니다!");
      }
      else {
        this.$router.push({name: 'read', params: {qnaNo: this.item.qnaNo}});
      }
    },
    moveList() {
      this.$router.push('/list');
    },
  },
}
</script>

